<template>
   
      <div class="wrapper">
      <div class="search">
        <span class="iconfont">&#xe62d;</span>
        <input class="search__area" @change="handsearchchange"
        placeholder="山姆会员商店"/>
       <div class="search__cancel" @click="handcancelseearch">取消</div>
      </div>
      <div class="area" v-if="history.length">
        <h4 class="area__title">
            搜索历史
            <span class="area__title__clear"
            @click="handclearhistory">清楚历史记录</span>
        </h4>
           <ul class="area__list">
            <li 
            v-for="item in history"
            :key="item"
            @click="()=>gotosearchlist(item)"
            class="area__list__item">{{item}}</li>
           </ul>
      </div>
      <div class="area">
        <h4 class="area__title">热门搜索</h4>
           <ul class="area__list">
            <li 
            v-for="item in hotwordList"
            :key="item"
            class="area__list__item"
            @click="()=>gotosearchlist(item)">{{item}}</li>
           </ul>
      </div>
      </div>
  </template>
  <script>
import{useRouter} from 'vue-router'
import {ref} from 'vue';
import { get } from '../../utils/request'


// 热词相关逻辑
const usehotwordlisteffect =()=>{
  const hotwordList = ref([])
   const gethotlist = async()=>{
    const result = await get('/api/shop/search/hot-words')
    if(result?.errno ===0 && result?.data?.length){
    hotwordList.value = result.data;
   
  }
   }
   return{gethotlist,hotwordList}
}
    export default {
        name:'search',
        setup(){
            const router = useRouter()
            const history = ref(JSON.parse(localStorage.history || '[]'))
            // 当用户搜索内容后，执行的操作
            const handsearchchange = (e)=>{
                const searchvalue = e.target.value
                if(!searchvalue) return;
                const hasvalue = history.value.find(item=>item === searchvalue);
                 if(!hasvalue){
                    history.value.push(searchvalue)
                    localStorage.history = JSON.stringify(history.value)
                 }
                 router.push(`/searchlist?keyword=${searchvalue}`)
            }
            // 当清除历史记录时
            const handclearhistory = ()=>{
                history.value = [];
                localStorage.history = JSON.stringify([])
            }
            const handcancelseearch =()=>{
                router.back();
            }
            // 页面跳转逻辑
            const gotosearchlist = (keyword) =>{
                router.push(`/searchlist?keyword = ${keyword}`)
            }
            // 使用热词逻辑
            const {gethotlist,hotwordList} = usehotwordlisteffect();
            gethotlist();
            return {handcancelseearch,handsearchchange,gotosearchlist,
                gethotlist,hotwordList,history,handclearhistory}
        }
    }
  </script>
  <style lang="scss" scoped>
    .wrapper{
     margin: 0 .18rem;
     a{
        text-decoration: none;
     }
     .search{
        display: flex;
        position: relative;
        line-height: 0.32rem;
        margin-top: .16rem;
        color: #333;
        .iconfont{
            position: absolute;
            left: 0.16rem;
            color: #b7b7b7;
        }
        &__area{
            flex: 1;
            padding: 0 .12rem 0 .44rem;
            background: #f5f5f5;
            border-radius: .16rem;
            border: none;
            outline: none;
            font-size: .14rem;
        }
        &__cancel{
            margin-left: .12rem;
            
            font-size: .16rem;
        }
    }
    .area{
        margin-top: .24rem;
        &__title{
            line-height: .22rem;
            margin: 0;
            font-size: .16rem;
            color: #333;
            font-weight: normal;
            &__clear{
                float: right;
                font-size: .14rem;
            }
        }
        &__list{
            padding: 0;
            margin: 0 0 0 -.1rem;
            list-style-type: none;
            &__item{
                display: inline-block;
                line-height: 0.32rem;
                font-size: .14rem;
                background: #f5f5f5;
                color: #666;
                border-radius: .02rem;
                margin-left: .1rem;
                margin-top: .12rem;
                padding: 0 .1rem;
            }
        }
    }
    }
    
    
  
  
  </style>
  